import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Tooltip from '@material-ui/core/Tooltip';
import NavButton from '../components/NavButton';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import TextField from '@material-ui/core/TextField';
import Box from '@material-ui/core/Box';

import backgrounds from '../static_data/backgrounds';
import talents from '../static_data/talents';
import equipment from '../static_data/equipment';

class Backgrounds extends React.Component {
  constructor(props) {
    super(props);

    this.handleTableClick = this.handleTableClick.bind(this);
  }

  handleTableClick(background) {
    this.props.setBackground(background);
  }

  areBackgroundInputsDisabled(background) {
    return(this.props.background === undefined || this.props.background.name !== background.name);
  }

  render() {
    return (
      <Box>
        <header>
        Select your background
        </header>
        
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Background</TableCell>
              <TableCell>Starting Skills</TableCell>
              <TableCell>Starting Talents</TableCell>
              <TableCell>Starting Equipment</TableCell>
              <TableCell>Bonus</TableCell>
              <TableCell>Aptitude</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              backgrounds.map(background => {
                const isRowSelected = (this.props.background !== undefined) && (background.name === this.props.background.name);
                return(
                  <TableRow
                    hover
                    onClick={() => this.handleTableClick(background)}
                    key={background.name}
                    selected={isRowSelected}
                  >
                    <TableCell>
                      {background.name}
                    </TableCell>
                    <TableCell>
                      {
                        background.skills.map(skill => {
                          if (skill.type === "choice") {
                            return(
                              <RadioGroup>
                                {
                                  skill.options.map(choice => (
                                    <FormControlLabel disabled={this.areBackgroundInputsDisabled(background)} value={choice.name} control={<Radio />} label={choice.name} />
                                  ))
                                }
                              </RadioGroup>
                            )
                          } else if (skill.type === "specialized") {
                            if (skill.specialization === null) {
                              return(
                                <Box>
                                  {skill.name} 
                                  <TextField id={background.name + "-" + skill.name + "-specialization"} placeholder="Pick one" disabled={this.areBackgroundInputsDisabled(background)} />
                                </Box>
                              )
                            } else {
                              return(
                                <Box>
                                  {skill.name} ({skill.specialization})
                                </Box>
                              )
                            }
                          } else {
                            return(
                              <Box>
                                {skill.name}
                              </Box>
                            )
                          }
                        })
                      }
                    </TableCell>
                    <TableCell>
                      {
                        background.talents.map(talent => {
                          if (talent.type === "choice") {
                            return(
                              <RadioGroup>
                                {
                                  talent.options.map(talentOption => {
                                    const talentHeader = talentOption.type === "specialized" ? talentOption.name + " (" + talentOption.specialization + ")" : talentOption.name;
                                    return(
                                      <Tooltip title={talents.find(item => item.name === talentOption.name).text}>
                                        <FormControlLabel disabled={this.areBackgroundInputsDisabled(background)} value={talentOption.name + "+" + talentOption.specialization} control={<Radio />} label={talentHeader} />
                                      </Tooltip>
                                    )
                                  })
                                }
                              </RadioGroup>
                            )
                          } else if (talent.type === "specialized") {
                            return(
                              <Tooltip title={talents.find(item => item.name === talent.name).text}>
                                {talent.name} ({talent.specialization})
                              </Tooltip>
                            )
                          } else {
                            return(
                              <Tooltip title={talents.find(item => item.name === talent.name).text}>
                                {talent.name}
                              </Tooltip>
                            )
                          }
                        })
                      }
                    </TableCell>
                    <TableCell>
                      {
                        background.equipment.map(backgroundItem => {
                          if (backgroundItem.type === "choice") {
                            return(
                              <RadioGroup>
                                {
                                  backgroundItem.options.map(itemOption => {
                                    return(
                                      <Tooltip title={equipment.find(item => item.name === itemOption.name).text}>
                                        <FormControlLabel disabled={this.areBackgroundInputsDisabled(background)} value={itemOption.name} control={<Radio />} label={itemOption.name} />
                                      </Tooltip>
                                    )
                                  })
                                }
                              </RadioGroup>
                            )
                          } else if (backgroundItem.type === "consumable") {
                            return(
                              <Tooltip title={equipment.find(item => item.name === backgroundItem.name).text}>
                                <Box>
                                  {backgroundItem.name} ({backgroundItem.count})
                                </Box>
                              </Tooltip>
                            )
                          } else {
                            return(
                              <Tooltip title={equipment.find(item => item.name === backgroundItem.name).text}>
                                <Box>
                                  {backgroundItem.name}
                                </Box>
                              </Tooltip>
                            )
                          }
                        })
                      }
                    </TableCell>
                    <Tooltip title={background.bonus.text}>
                      <TableCell>
                        {background.bonus.name}
                      </TableCell>
                    </Tooltip>
                    <TableCell>
                      <RadioGroup>
                        {
                          background.aptitudes.map(aptitude => (
                            <FormControlLabel disabled={this.areBackgroundInputsDisabled(background)} value={aptitude} control={<Radio />} label={aptitude} />
                          ))
                        }
                      </RadioGroup>
                    </TableCell>
                  </TableRow>
                )
              })
            }
          </TableBody>
        </Table>
        <NavButton to={"/characteristics"}>
          Back
        </NavButton>
        <NavButton disabled={this.props.background === undefined} >
          Choose your role
        </NavButton>
      </Box>
    );
  }
}

export default Backgrounds;