import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Tooltip from '@material-ui/core/Tooltip';
import NavButton from '../components/NavButton';
import Box from '@material-ui/core/Box';

import homeworlds from '../static_data/homeworlds';

class  Homeworlds extends React.Component {
  constructor(props) {
    super(props);

    this.handleTableClick = this.handleTableClick.bind(this);
  }

  handleTableClick(world) {
    this.props.setHomeworld(world);
  }

  render() {
    return (
      <Box>
        <header>
        Select your homeworld
        </header>
        
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>World</TableCell>
              <TableCell>Characterstic Modifiers</TableCell>
              <TableCell>Fate Threshold</TableCell>
              <TableCell>Bonus</TableCell>
              <TableCell>Aptitude</TableCell>
              <TableCell>Wounds</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              homeworlds.map(world => {
                const isRowSelected = (this.props.homeworld !== undefined) && (world.name === this.props.homeworld.name);
                return(
                  <TableRow
                    hover
                    onClick={() => this.handleTableClick(world)}
                    key={world.name}
                    selected={isRowSelected}
                  >
                    <TableCell>
                      {world.name}
                    </TableCell>
                    <TableCell>
                      {
                        world.modifiers.map(modifier => (
                          <Box>
                            {modifier.sign + modifier.characteristic}
                          </Box>
                        ))
                      }
                    </TableCell>
                    <TableCell>
                      {world.fate.threshold} (Emperor's Blessing: {world.fate.emperors_blessing}+)
                    </TableCell>
                    <Tooltip title={world.bonus.text}>
                      <TableCell>
                        {world.bonus.name}
                      </TableCell>
                    </Tooltip>
                    <TableCell>
                      {world.aptitude}
                    </TableCell>
                    <TableCell>
                      {world.wounds}+1d5
                    </TableCell>
                  </TableRow>
                )
              })
            }
          </TableBody>
        </Table>
        <NavButton to={"/"}>
          Back
        </NavButton>
        <NavButton disabled={this.props.homeworld === undefined} to={"/characteristics"} >
          Characteristics
        </NavButton>
      </Box>
    );
  }
}

export default Homeworlds;