import React from 'react';
import { Button } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import NavButton from '../components/NavButton';
import Box from '@material-ui/core/Box';

class Characteristics extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      woundsRolled: false,
      fateRolled: false
    };

    this.rollForWounds = this.rollForWounds.bind(this);
    this.rollForFate = this.rollForFate.bind(this);
  }
  
  rollForWounds() {
    this.props.setWounds(Math.floor(Math.random() * 5) + 1 + this.props.homeworld.wounds);
    this.setState({
      ...this.state,
      woundsRolled: true
    });
  }

  rollForFate() {
    const roll = Math.floor(Math.random() * 10) + 1;

    if (roll >= this.props.homeworld.fate.emperors_blessing) {
      this.props.setFate(this.props.homeworld.fate.threshold + 1);
    } else {
      this.props.setFate(this.props.homeworld.fate.threshold);
    }

    this.setState({
      ...this.state,
      fateRolled: true
    });
  }

  render() {
    return (
      <Box>
        <Grid container>
          {
            Object.keys(this.props.characteristics).map(characteristic => (
              <Grid item xs={6}>
                {characteristic}
                <TextField
                  id={characteristic}
                  type="number"
                  value={this.props.characteristics[characteristic]}
                  onChange={event => this.props.handleCharacteristicChange(event, characteristic)}
                />
              </Grid>
            ))
          }
        </Grid>
  
        <Box>
          Wounds
          <TextField id="wounds" disabled type="number" value={this.props.wounds} />
          <Button disabled={this.state.woundsRolled} onClick={() => { this.rollForWounds(); }}>
            Roll for wounds!
          </Button>
        </Box>
  
        <Box>
          Fate threshold
          <TextField id="fate" disabled type="number" value={this.props.fate} />
          <Button disabled={this.state.fateRolled} onClick={() => { this.rollForFate(); }}>
            Roll for Emperor's Blessing!
          </Button>
        </Box>
  
        <Box>
          Movement
          <Box>
            Half
            <TextField id="movement-half" disabled type="number" value={Math.floor(this.props.characteristics["agility"] / 10)} />
          </Box>
          <Box>
            Full
            <TextField id="movement-full" disabled type="number" value={Math.floor(this.props.characteristics["agility"] / 10) * 2} />
          </Box>
          <Box>
            Charge
            <TextField id="movement-charge" disabled type="number" value={Math.floor(this.props.characteristics["agility"] / 10) * 3} />
          </Box>
          <Box>
            Run
            <TextField id="movement-run" disabled type="number" value={Math.floor(this.props.characteristics["agility"] / 10) * 6} />
          </Box>
        </Box>
  
        <Box>
          Fatigue
          <TextField id="fatigue" disabled type="number" value={Math.floor(this.props.characteristics["toughness"] / 10) + Math.floor(this.props.characteristics["willpower"] / 10)} />
        </Box>
  
        <Box>
          Max carry
          <TextField id="fatigue" disabled type="number" value={Math.floor(this.props.characteristics["toughness"] / 10) + Math.floor(this.props.characteristics["strength"] / 10)} />
        </Box>
  
        <NavButton to={"/homeworlds"}>
          Back
        </NavButton>
        <NavButton to={"/backgrounds"}>
          Select your background
        </NavButton>
      </Box>
    );
  }
}

export default Characteristics;