import React from 'react';
import './App.css';
import CharacterCreator from './views/CharacterCreator';
import Homeworlds from './views/Homeworlds';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import Characteristics from './views/Characteristics';
import Backgrounds from './views/Backgrounds';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      homeworld: undefined,
      characteristics: {
        "weapon_skill": 0,
        "ballistic_skill": 0,
        "strength": 0,
        "toughness": 0,
        "agility": 0,
        "intelligence": 0,
        "perception": 0,
        "willpower": 0,
        "fellowship": 0,
        "influence": 0
      },
      wounds: undefined,
      fate: undefined,
      background: undefined
    };

    this.setHomeworld = this.setHomeworld.bind(this);
    this.setCharacteristics = this.setCharacteristics.bind(this);
    this.handleCharacteristicChange = this.handleCharacteristicChange.bind(this);
    this.setWounds = this.setWounds.bind(this);
    this.setFate = this.setFate.bind(this);
    this.setBackground = this.setBackground.bind(this);
  }

  setHomeworld(homeworld) {
    this.setState({
      ...this.state,
      homeworld: homeworld
    });
  }

  setCharacteristics(characteristics) {
    this.setState({
      ...this.state,
      characteristics: characteristics
    });
  }

  handleCharacteristicChange(event, characteristic) {
    this.setCharacteristics({
      ...this.state.characteristics, 
      [characteristic]: event.target.value
    });
  }

  setWounds(wounds) {
    this.setState({
      ...this.state,
      wounds: wounds
    });
  }

  setFate(fate) {
    this.setState({
      ...this.state,
      fate: fate
    });
  }

  setBackground(background) {
    this.setState({
      ...this.state,
      background: background
    });
  }

  render() {
    return (
      <Router>
        <Route path="/" exact component={CharacterCreator} />
        <Route path="/homeworlds/" render={(props) => <Homeworlds {...props} homeworld={this.state.homeworld} setHomeworld={this.setHomeworld}/>} />
        <Route path="/characteristics/" render={(props) => <Characteristics {...props} homeworld={this.state.homeworld} characteristics={this.state.characteristics} fate={this.state.fate} wounds={this.state.wounds} handleCharacteristicChange={this.handleCharacteristicChange} setFate={this.setFate} setWounds={this.setWounds} />} />
        <Route path="/backgrounds/" render={(props) => <Backgrounds {...props} background={this.state.background} setBackground={this.setBackground} />} />
      </Router>
    );
  }
}

export default App;
